angular.module("ToolM", [])
.provider("ToolProvider", function () {
        this.$get = function () {
            var self = this;
            // 利用post 到某頁面
            self.doPOST = function (url, obj, element) {
                var form = document.createElement("form");
                if (element) {
                    form = element;
                }
                form.setAttribute("method", "post");
                form.setAttribute("action", url);
                form.setAttribute("style", "display:none;");

                for (var key in obj) {
                    self.addInput(key, obj[key], form);
                }

                if (!element) {
                    document.body.appendChild(form);
                }

                form.submit();
            }

            self.addInput = function (key, value, element) {
                var hf = document.createElement("input");
                var hf2 = document.querySelector("input[name='" + key + "']");
                if (hf2)
                {
                    hf = hf2;
                }
                hf.setAttribute("type", "hidden");
                hf.setAttribute("name", key);
                hf.setAttribute("value", value);
                if (!hf2) {
                    element.appendChild(hf);
                }
               
            }

            self.popUpWindow = function (url, title, w, h) {
                var left = (screen.width / 2) - (w / 2);
                var top = (screen.height / 2) - (h / 2);
                return window.open(url, title, 'toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=' + w + ', height=' + h + ', top=' + top + ', left=' + left);
            }

            self.openWindowWithPost = function (url, title, w, h, obj) {
                var newWindow = self.popUpWindow(url, title, w, h);
                if (!newWindow) return false;
                var html = "";
                html += "<html><head></head><body><form id='formid' method='post' action='" + url + "' autocomplete='off'>";
                for (var key in obj) {
                    html += "<input type='hidden' name='" + key + "' value='" + obj[key] + "'/>";
                }
                html += "</form><script>document.getElementById('formid').submit();" + "</" + "script" + "></" + "body" + "></" + "html>";
                newWindow.document.write(html);
                return newWindow;
            }

            self.popPrint = function (data, cssAry) {
                cssAry = cssAry || [];
                var mywindow = popUpWindow("", "Print", 800, 600);

                mywindow.document.write('<html><head><title>列印</title>');
                /*optional stylesheet*/

                for (var i = 0; i < cssAry.length; i++) {
                    mywindow.document.write('<link rel="stylesheet" href="' + cssAry[i] + '" type="text/css" />');
                }
                mywindow.document.write('</head><body class="bodybg">');
                mywindow.document.body.innerHTML = '';
                mywindow.document.write(data);
                mywindow.document.write('</body></html>');

                setTimeout(function () {
                    document.close(); // necessary for IE >= 10
                    mywindow.focus(); // necessary for IE >= 10

                    mywindow.print();
                    mywindow.close();
                }, 500);

                return true;
            };

            self.setData = function (data) {
                try
                {
                    location.hash = encodeURIComponent(JSON.stringify(data));
                }
                catch(ex)
                {

                }
            };

            self.getData = function () {
                try {
                    return JSON.parse(decodeURIComponent(location.hash.substring(1)));
                }
                catch (ex) {
                    return "";
                }
            };

            return self;


        };
});

